@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Poppins', sans-serif;
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;    
  align-items: center;
  background: rgb(128,128,128);
  background: linear-gradient(0deg, rgba(128,128,128,1) 0%, rgba(0,0,0,1) 0%, rgba(48,48,48,1) 100%);
}

hr.hor-line {
  border: px solid rgb(77, 77, 77);
  width: 80%;
}

p {
  font-size: 1em;
  margin: 10px;
  border: none;
  background: none;
  color: rgb(156, 156, 156);
}

select:focus {
  outline: none;
}

option {
  font-size: 1em;
  margin: 10px;
  border: none;
  background-color: rgb(31, 31, 31);
  color: rgb(156, 156, 156);
}

#add-event {
  margin: 5px;
  text-align: center;
  visibility: visible;
  height: 40px;
  width: 200px;
  background-color: rgb(124, 124, 124);
  color: rgb(51, 51, 51);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

#add-event:hover {
  background-color: rgb(158, 158, 158);
}

#info-app {
  position: fixed;
  right: 5px;
  bottom: 0px;
  color: rgb(143, 143, 143);
  font-size: 10px;
}
.add-event-popup {
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-animation-name: anim2;
            animation-name: anim2;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.active {
    visibility: visible;
    -webkit-animation-name: anim;
            animation-name: anim;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes anim {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes anim {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes anim2 {
    from {
        visibility: visible;
        opacity: 1;
    }
    to {
        opacity: 0;
        visibility: hidden;
    }
}

@keyframes anim2 {
    from {
        visibility: visible;
        opacity: 1;
    }
    to {
        opacity: 0;
        visibility: hidden;
    }
}

.glass {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.99);
    position: fixed;
    z-index: 2;
    opacity: 0.5;
}

.container {
    opacity: 0;
    z-index: 3;
    height: 250px;
    width: 300px;
    background: rgb(114, 114, 114);
    background: linear-gradient(0deg, rgba(128,128,128,1) 0%, rgba(0,0,0,1) 0%, rgba(48,48,48,1) 100%);
    border: 1px solid rgb(177, 177, 177);
    border-radius: 5px;
    box-shadow: 0px 0px 16pc 0px rgba(0,0,0.9);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    -webkit-animation-name: close;
            animation-name: close;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.container-active {
    opacity: 1;
    height: 250px;
    -webkit-animation-name: open;
            animation-name: open;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    transition: all 0.5s;
}

@-webkit-keyframes open {
    from {
        opacity: 0;
        transform: translate(0px, 100px);
    }
    to {
        opacity: 1;
        transform: translate(0px, 0px);
    }
}

@keyframes open {
    from {
        opacity: 0;
        transform: translate(0px, 100px);
    }
    to {
        opacity: 1;
        transform: translate(0px, 0px);
    }
}

@-webkit-keyframes close {
    from {
        opacity: 1;
        transform: translate(0px, 0px);
    }
    to {
        opacity: 0;
        transform: translate(0px, 100px);
    }
}

@keyframes close {
    from {
        opacity: 1;
        transform: translate(0px, 0px);
    }
    to {
        opacity: 0;
        transform: translate(0px, 100px);
    }
}

form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}

input {
    padding: 5px;
    width: 250px;
    background-color: transparent;
    border: 1px solid white;
    border-radius: 5px;
    color: white;
}

input:focus {
    background: transparent;
    outline: none;
}

hr {
    margin-top: 10px;
    background-color: rgb(77, 77, 77);
    border: none;
    height: 2px;
    width: 300px;
}

#buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.container button {
    width: 90px;
    border: 1px solid white;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
    visibility: visible;
    color: white;
}

.container button:hover {
    background-color: gray;
    color: black;
}

.event-item {
    margin: 5px 10px;
    min-height: 50px;
    border: 1px solid  rgb(77, 77, 77);
    border-radius: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    transition: 0.3s;
    position: relative;
    overflow: visible;
}

.event-item:first-child {
    margin-top: 3px;
}
.event-item p {
    color:  rgb(194, 194, 194);
}
.event-name {
    transition: 0.4s;
}
.event-item:hover {
    color: rgb(255, 255, 255);
    border: 1px solid  rgb(117, 117, 117);
    background: rgb(53, 53, 53);
}
.event-item:hover .event-name {
    font-size: 1.1em;
}

.event-item:hover p {
    color: white;
}

.event-item:hover button {
    visibility: visible;
}

.event-name {
    width: 100%;
    padding: 0 5px 0 10px;
}

.time {
    color: rgb(156, 156, 156);
    width: 135px;
    text-align: left;
    position: relative;
    overflow: visible;

}

button {
    visibility: hidden;
    color: rgb(204, 107, 107);
    padding: 0 20px 0 0px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

button:focus {
    outline: none;
}

button:hover {
    color: rgb(255, 179, 179);
}

.tooltip .tooltiptext {
    font-size: 10px;
    visibility: hidden;
    padding: 2px 10px 15px 10px;
    height: 10px;
    width: 100px;
    border: 1px solid rgb(165, 165, 165);
    background-color: rgb(32, 32, 32);
    color: #fff;
    text-align: center;
    border-radius: 6px;
  
    /* Position the tooltip */
    position: absolute;
    left: 0;
    bottom: 85%;
}

.tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: black transparent transparent transparent;
  }
  
.tooltip:hover .tooltiptext {
    visibility: visible;
}
  
.tooltip:hover .tooltiptext {
    visibility: visible;
}

@media only screen and (max-width: 600px) {
    button {
        visibility: visible;
    }
}
.events-list {
    max-height: 51vh;
    overflow-x: hidden;
    overflow-y: auto;
    width: 85vw;
    margin-bottom: 10px;
    margin-top: -5px;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: rgb(77, 77, 77);
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(180, 180, 180); 
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(255, 255, 255); 
  }

  @media only screen and (max-width: 600px) {
    .events-list {
      max-height: 60vh;
    }
  }
